
.appBar {
    background-color: #2B2F3E;
    color: #808080;
    min-height: 100vh;
    padding: 1rem 1.6rem;
    flex: 0 0 21%;
}

.appBar h1 {
    font-weight: 300;
    text-transform: uppercase;
    font-size: 2.2rem;
    color: #fff;
}


.addItemsForm .form-group {
    flex-grow: 1;
    margin-right: 1rem;
}

.addItemsForm .btn {
    padding-right: 2rem;
    padding-left: 2rem;
}
.itemsList {
    margin: -0.3rem 0 0 0;
    padding: 0;
    list-style-type: none;
    color: #2b2f3e;
}
.itemsList__item {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem 0 0.5rem 0.2rem;
    border-bottom: 1px solid rgba(0,0,0,0.1);
}
.itemsList__item::before {
    content: '';
    position: absolute;
    top: 50%;
    left: -1.6rem;
    height: 0;
    width: 0.3rem;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    opacity: 0;
    -webkit-transition: height 0.3s, opacity 0.3s;
    transition: height 0.3s, opacity 0.3s;
    background-color: #ff2f5a;
}
.itemsList__item.active::before {
    height: 100%;
    opacity: 1;
}
.itemsList__item .btn {
    padding: .3rem 1.5rem;
}
.itemsList__name {
    display: flex;
    align-items: center;
    margin-right: 1rem;
    line-height: 1.3;
    cursor: pointer;
}
.itemsList__name .name {
    margin-right: 1.5rem;
    font-weight: 600;
}
.itemsList__name .badge {
    padding: .3em .8em;
    border-radius: .7rem;
}

.commentsForm {
    display: flex;
}
.commentsForm__avatar {
    width: 3.2rem;
    height: 3.2rem;
    flex: 0 0 3.2rem;
    background-color: #e6e6e6;
    margin-right: 0.8rem;
}
.commentsForm .form-group {
    flex-grow: 1;
    margin-bottom: 0;
}
.commentsForm .form-control {
    font-size: 0.8rem;
    resize: none;
}
.commentsList {
    margin: 0;
    padding: 0;
    list-style-type: none;
    color: #666666;
}

.commentsList__item {
    display: flex;
    padding: 1.2rem 0;
    border-bottom: 1px solid rgba(0,0,0,0.1);
}

.commentsList__item:last-child {
    border-bottom: none;
}

.commentsList__item p {
    font-size: 0.76rem;
    line-height: 1.5;
    margin: -0.25rem 0 0;
}

.commentsList__avatar {
    width: 3.2rem;
    height: 3.2rem;
    flex: 0 0 3.2rem;
    background-color: #ff8a00;
    margin-right: 0.8rem;
}
.commentsList__item:nth-child(2n) .commentsList__avatar {
    background-color: #47568c;
}


body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F9F9F9;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app-wp {
  padding: 1.6rem;
  flex-grow: 1;
}

.app-wp > div {
  padding: 0.5rem;
}

.ItemsWrap {
  flex: 0 0 52%;
}

.CommentsWrap {
  flex: 0 0 48%;
}

.Card {
  background-color: #fff;
  box-shadow: 0 0 0.9rem rgba(0,0,0,0.3);
  border-radius: 0.2rem;
  padding: 1rem;
  min-height: 33rem
}

.Card--items {
  padding-left: 1.5rem;
}
.Card--comments {
  padding-right: 1.5rem;
}

.Card__title {
  font-size: 2.2rem;
  font-weight: 500;
  line-height: 1;
  margin-bottom: 1.4rem;
  color: #565a69;
}


